.dogmaAttributeResistance {
  margin: 0;
  width: 25%;
  text-align: center;
  float: left;
  display: inline-block;
}
.emResistance {
  background-color: rgba(51,153,255,0.3);
}
.emResistance .resistanceInner {
  background-color: #39f;
}
.thermalResistance {
  background-color: rgba(180,0,0,0.3);
}
.thermalResistance .resistanceInner {
  background-color: #b40000;
}
.explosiveResistance {
  background-color: rgba(200,150,58,0.3);
}
.explosiveResistance .resistanceInner {
  background-color: #c8963a;
}
.kineticResistance {
  background-color: rgba(178,178,178,0.3);
}
.kineticResistance .resistanceInner {
  background-color: #b2b2b2;
}


